"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import {
    getPreferences,
    setPreferences,
    setActiveSession,
    getActiveSession,
} from "@/lib/storage";

export default function HomePage() {
    const router = useRouter();
    const [duration, setDuration] = useState(25);
    const [brownNoise, setBrownNoise] = useState(false);
    const [loading, setLoading] = useState(false);
    const [ready, setReady] = useState(false);

    useEffect(() => {
        setReady(true);
        const prefs = getPreferences();
        setBrownNoise(prefs.brownNoise);
        setDuration(prefs.lastDuration || 25);

        if (getActiveSession()) {
            router.push("/focus");
        }
    }, [router]);

    const start = () => {
        setLoading(true);
        setPreferences({ brownNoise, lastDuration: duration });
        setActiveSession({
            startTime: Date.now(),
            duration,
            quitAttempts: 0,
            brownNoise,
        });
        router.push("/focus");
    };

    if (!ready) {
        return (
            <div className="page">
                <div className="spinner" />
            </div>
        );
    }

    return (
        <div className="page">
            <div className="content text-center fade-in">
                <h1 className="mb-3">Time to focus</h1>
                <p className="mb-10">Pick a duration and get started.</p>

                <div className="durations mb-8">
                    {[25, 50, 90].map((min) => (
                        <button
                            key={min}
                            onClick={() => min !== 90 && setDuration(min)}
                            className={`duration ${duration === min ? "active" : ""} ${min === 90 ? "locked" : ""}`}
                            disabled={min === 90}
                        >
                            {min} min
                        </button>
                    ))}
                </div>

                <div className="toggle-row mb-10">
                    <button
                        className={`toggle ${brownNoise ? "on" : ""}`}
                        onClick={() => {
                            const val = !brownNoise;
                            setBrownNoise(val);
                            setPreferences({ brownNoise: val });
                        }}
                        aria-label="Toggle brown noise"
                    />
                    <span className="small">Brown noise</span>
                </div>

                <button onClick={start} disabled={loading} className="btn">
                    Lock In
                </button>

                <p className="mt-16">
                    <a href="/pricing" className="link">View pricing</a>
                </p>
            </div>
        </div>
    );
}
